* {
    border: none;
    outline: none;
    margin: 0;
    padding: 0;
    transition: 0.3s;
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    box-sizing: border-box;
  }
  .container {
      max-width: 1366px;
      width: 100%;
      margin: 0 auto;
      margin-top: 5rem;
      color:white;
    }
#customers {
    
    border-collapse: collapse;
    width: 100%;
  }
  
  #customers td, #customers th {
    border: 1px solid rgb(24, 24, 24);
    padding: 8px;
  }
  
  #customers tr:nth-child(even){background-color: #0e0e0e;}
  
  #customers tr:hover {background-color: rgb(24, 24, 24);}
  
  #customers th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color: #1f1f1f;
    color: white;
  }